@import './styles/reset';
@import './styles/mixins';
@import './styles/grid';

html {
  height: 100%;
  scroll-behavior: smooth;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
  margin: 0;
}

body {
  font-family: 'Mont', 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell',
    'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  color: black;
  font-size: 14px;
}

.page {
  display: flex;
  justify-content: center;
  &__container {
    padding: 25px 16px 0 16px;
    width: 320px;
    height: 100%;

    @include onTablet {
      padding: 25px 24px 0 24px;
      width: 640px;
    }

    @include onLaptop {
      width: 768px;
    }

    @include onDesktop {
      padding: 50px 75px 80px 75px;
      width: 1200px;
    }
  }
}

