@import '../../styles/mixins';

.postCard {
  display: flex;
  flex-direction: column;
  width: 340px;
  border: 1px solid black;
  padding: 25px;
  transition: all 0.2s ease;

  @include onLaptop {
    width: 380px;
  }

  @include onTablet {
    width: 370px;
  }

  @include onDesktop {
    width: 400px;
  }

  &:hover {
    box-shadow: 0px 3px 13px rgba(23, 32, 49, 0.4);
    transform: scale(1.01);
  }

  &__title {
    color:#363636;
    margin-bottom: 15px;
    font-weight: 400;

    @include onLaptop {
      line-height: 28px;
      font-size: 21px;
      margin-bottom: 20px;
    }

    @include onTablet {
      line-height: 26px;
      font-size: 20px;
      margin-bottom: 18px;
    }

    @include onDesktop {
      line-height: 30px;
      font-size: 24px;
      margin-bottom: 20px;
    }
  }

  &__link {
    color: black;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;

    &_text {
      font-weight: 400;
      font-size: 24px;
    }

    &_img {
      margin-bottom: 25px;
      height: 130px;
      display: block;

      @include onTablet {
        height: 196px;
      }

      @include onDesktop {
        height: 217px;
      }
    }
  }

  &__date {
    color: #363636;
    font-size: 14px;
    line-height: 21px;
    font-weight: 400;

    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;

    @include onLaptop {
      width: 380px;
    }

    @include onTablet {
      width: 370px;
    }

    @include onDesktop {
      width: 400px;
    }
  }

  &__content {
    font-size: 16px;
    line-height: 24px;

    @include onLaptop {
    }

    @include onTablet {
    }

    @include onDesktop {
    }
  }
}
